import React, { useEffect, lazy, Suspense} from 'react'
import { Route, Switch} from 'react-router-dom'
import './App.css'
import Header from './components/header/header.component'
import HomeContextProvider from './store/HomeContext'
import GeneralContextProvider from './store/GeneralContext'
import PageContainer from './pages/page-container.component'
import AboutContextProvider from './store/AboutContext';
import ContactContextProvider from './store/ContactContext';


const ContactPage = lazy(() => import('./pages/contact.component'))
const HomePage = lazy(() => import('./pages/home.component'))
const AboutPage = lazy(() => import('./pages/about.component'))
function App() {
  useEffect(() => {
    window.scrollTo(0,0)
}, [])
  return (
  
       <div className="App">
           <GeneralContextProvider>
             <PageContainer>
              <HomeContextProvider>
                <Header/>
                <Switch>
                <Suspense fallback={<div>Loadding...</div>}>
                  <Route path={'/'} exact>
                    <HomePage/>
                  </Route>
                  <AboutContextProvider>
                  <Route path={'/about'} component={AboutPage}/>
                  </AboutContextProvider>
                  <ContactContextProvider>
                    <Route path={'/contact'} component={ContactPage} />
                  </ContactContextProvider>
                </Suspense>
                </Switch>
              
              </HomeContextProvider>
             
             </PageContainer>
           </GeneralContextProvider> 
        </div>

   
  );
}

export default App;
